const axios = require('axios');

// Replace with your Google Sheets API key
const API_KEY = process.env.REACT_APP_GOOGLEAPIKEY;

// Replace with your Google Spreadsheet ID
const SPREADSHEET_ID = process.env.REACT_APP_GOOGLESHEETID;

// Google Sheets API endpoint
const API_ENDPOINT = `https://sheets.googleapis.com/v4/spreadsheets/${SPREADSHEET_ID}/values/Sheet1?key=${API_KEY}`;

async function getCurrentTimeInTimeZone(timezone) {
  const now = new Date();
  const currentTime = new Date(now.toLocaleString('en-US', { timeZone: timezone }));
  return currentTime.toISOString();
}

async function saveToGoogleSheets(userEmail) {
  try {
    const brazilTimezone = 'Etc/GMT+3'; // 3 hours ahead of UTC
    const currentTime = await getCurrentTimeInTimeZone(brazilTimezone);

    // Data to be saved to the Google Spreadsheet
    const rowData = [userEmail, currentTime];

    // Send a POST request to append data to the spreadsheet
    const response = await axios.post(API_ENDPOINT, {
      values: [rowData],
    });

    console.log('Data appended to Google Sheets successfully:', response.data);
  } catch (error) {
    console.error('Error appending data to Google Sheets:', error.message);
  }
}

//// Usage example
//const data = {
//  userEmail: 'leao.rafinha@gmail.com', // Replace with the user's email
//};
